import { getQuery, parseQuery } from 'api/queries/ocpQuery';
import { getProvidersQuery } from 'api/queries/providersQuery';
import { breakdownDescKey } from 'api/queries/query';
import BreakdownBase from 'pages/details/components/breakdown/breakdownBase';
import { getGroupById, getGroupByValue } from 'pages/details/components/utils/groupBy';
import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { paths } from 'routes';
import { createMapStateToProps } from 'store/common';
import { ocpProvidersQuery, providersSelectors } from 'store/providers';
import { reportActions, reportSelectors } from 'store/reports';
import { CostOverview } from './costOverview';
import { HistoricalData } from './historicalData';
const detailsURL = paths.ocpDetails;
const reportType = "cost" /* cost */;
const reportPathsType = "ocp" /* ocp */;
// eslint-disable-next-line @typescript-eslint/no-unused-vars
const mapStateToProps = createMapStateToProps((state, props) => {
    const queryFromRoute = parseQuery(location.search);
    const query = queryFromRoute;
    const filterBy = getGroupByValue(query);
    const groupBy = getGroupById(query);
    const newQuery = Object.assign(Object.assign({}, query), { [breakdownDescKey]: undefined });
    const queryString = getQuery(newQuery);
    const report = reportSelectors.selectReport(state, reportPathsType, reportType, queryString);
    const reportError = reportSelectors.selectReportError(state, reportPathsType, reportType, queryString);
    const reportFetchStatus = reportSelectors.selectReportFetchStatus(state, reportPathsType, reportType, queryString);
    const providersQueryString = getProvidersQuery(ocpProvidersQuery);
    const providers = providersSelectors.selectProviders(state, "ocp" /* ocp */, providersQueryString);
    const providersFetchStatus = providersSelectors.selectProvidersFetchStatus(state, "ocp" /* ocp */, providersQueryString);
    return {
        costOverviewComponent: React.createElement(CostOverview, { filterBy: filterBy, groupBy: groupBy, report: report }),
        description: query[breakdownDescKey],
        detailsURL,
        emptyStateTitle: props.t('navigation.ocp_details'),
        filterBy,
        groupBy,
        historicalDataComponent: React.createElement(HistoricalData, { filterBy: filterBy, groupBy: groupBy }),
        providers,
        providersFetchStatus,
        providerType: "ocp" /* ocp */,
        query,
        queryString,
        report,
        reportError,
        reportFetchStatus,
        reportType,
        reportPathsType,
        tagReportPathsType: "ocp" /* ocp */,
        title: filterBy,
    };
});
const mapDispatchToProps = {
    fetchReport: reportActions.fetchReport,
};
const OcpBreakdown = withTranslation()(connect(mapStateToProps, mapDispatchToProps)(BreakdownBase));
export default OcpBreakdown;
